import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import {BrowserRouter, HashRouter} from "react-router-dom"
import {Provider} from "react-redux"
import {createStore, combineReducers, applyMiddleware, compose} from "redux"
import thunk from "redux-thunk"
import { burgerBuilderReducer }  from "./store/reducers/burgerBuilderReducer"
import authReducer from "../src/store/reducers/auth"

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(combineReducers({burgerBuilderReducer,authReducer}),composeEnhancers(applyMiddleware(thunk)))
ReactDOM.render(<Provider store={store}><HashRouter><App /></HashRouter></Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
